// =============================================================================
// Webhook Delivery Logs API
// =============================================================================
// GET /api/v1/webhooks/[webhookId]/deliveries
// 
// Get delivery history for a webhook

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { getDeliveryRecords } from '@/db/webhook-deliveries';

/**
 * GET /api/v1/webhooks/[webhookId]/deliveries
 * 
 * Get delivery history for a webhook
 */
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ webhookId: string }> }
) {
  const { webhookId } = await params; // Next.js 15+ requires awaiting params
  const ctx = await extractRequestContext(request, `/api/v1/webhooks/${webhookId}/deliveries`);
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    ctx.apiKeyId = authResult.apiKey.id;
    
    const searchParams = request.nextUrl.searchParams;
    const status = searchParams.get('status');
    const limit = searchParams.get('limit') ? parseInt(searchParams.get('limit')!) : 100;
    
    const deliveries = await getDeliveryRecords(webhookId, {
      status: status || undefined,
      limit,
    });
    
    return NextResponse.json({
      webhookId: webhookId,
      deliveries: deliveries.map(d => ({
        id: d.id,
        eventId: d.event_id,
        eventType: d.event_type,
        status: d.status,
        attempts: d.attempts,
        maxAttempts: d.max_attempts,
        lastAttemptAt: d.last_attempt_at,
        lastResponseCode: d.last_response_code,
        errorMessage: d.error_message,
        deliveredAt: d.delivered_at,
        createdAt: d.created_at,
      })),
      count: deliveries.length,
    });
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Internal server error'),
      500,
      'INTERNAL_ERROR'
    );
  }
}






